import React, { useEffect, useState, useLayoutEffect} from 'react';
import './serviceStyle.css';
import Swal from "sweetalert2";
import { AddDiagnosticCenter, GetAllStores } from '../../lib/axios';
const AddService = () => {

    const [formData, setFormData] = React.useState()
    const [storeList, setStoreList] = React.useState(null)
    const [isSaving, setIsSaving] = useState(false);

    const handleGetAllStores = () => {
        GetAllStores()
            .then((response) => {
                setStoreList(response.stores);
            })
    }
    useLayoutEffect(() => {
        handleGetAllStores();
    },[]);

    function handleChange(event) {
        setFormData(prevFormData => {
            return {
                ...prevFormData,
                [event.target.name]: event.target.value
            }
        })
    }

    function handleAdd(event) {
        setIsSaving(true);
        let user = JSON.parse(localStorage.getItem('user'));
        formData.userId = user.userId;
        AddDiagnosticCenter(formData)
			.then(function (response) {
					
				Swal.fire({
					icon: 'success',
					title: response.responseMessage,
					showConfirmButton: false,
					timer: 1500
				})
				
					setIsSaving(false);
					// setPhone('');
					// setEmail('');
				})
				.catch(function (error) {
					Swal.fire({
						icon: 'error',
						title: error.response.data.responseMessage,
						showConfirmButton: false,
						timer: 1500
					})
					// setIsSaving(false)
				});
    }

    return (

        // <div>
            <div className='service-top'>
                <div className="row">
                    <div className="d_box table-responsive">
                        <form action="" method="POST">
                            <div className="modal-body">
                                <div className="up_service d-flex">
                                    <div className="up_serv_item pr-1">
                                        {/* <input type="text" name="serv1" placeholder="Searvice Name" required /> */}
                                        <div className="input-group input-group-sm mb-3">
                                            <input type="text" className="form-control" placeholder="Searvice Name" aria-label="Sizing example input" name="serviceName" aria-describedby="inputGroup-sizing-sm" onChange={handleChange}
/>
                                        </div>
                                        <div className="input-group input-group-sm mb-3">
                                            <input type="text" className="form-control" placeholder="Searvice box category" aria-label="Sizing example input" name="serviceCategory" aria-describedby="inputGroup-sizing-sm" onChange={handleChange}/>
                                        </div>
                                        <div className="input-group input-group-sm mb-3">
                                            <input type="text" className="form-control" placeholder="Searvice ID"  aria-label="Sizing example input" name="serviceId" aria-describedby="inputGroup-sizing-sm" onChange={handleChange}/>
                                        </div>
                                        <div className="input-group input-group-sm mb-3">
                                        <label className="input-group-text" htmlFor="inputGroupSelect01">Store ID</label>
                                            <select className="form-select" name="storeId" id="inputGroupSelect01" onChange={handleChange}>
                                                {                                                  
                                                    storeList?.map((val, key) => (
                                                        <option key={key} value={val.id}>{val.name}</option>
                                                    ))                                                  
                                                };                                            
                                            </select>
                                        </div>

                                    </div>
                                    {/* <span className="btn btn-success  servBoxBtn plusBoxBtn" title="Add" servBoxCount="2"><i className="fas fa-plus"></i></span> */}
                                </div>
                                <div className="plusServBox w-100 diBlock"></div>
                            </div>
                            <div className="modal-footer">
                                <input type="submit" name="" className="btn btn-success bg_self" value="Add" onClick={handleAdd} disabled={isSaving} />
                            </div>
                        </form>
                    </div>
                </div>
                <div className="col-md-3 col-sm-2 col-xs-12"></div>
            </div>

       
        // </div>
    )
}

export default AddService
