import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import telephone from "../../images/telephone-symbol-button.png";
import location from "../../images/location-white.png";
// import logo from "../images/logo.png";
import logo1 from "../../images/logo1.jpeg";
import Swal from "sweetalert2";
// import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
// import { faCoffee } from "@fortawesome/fontawesome-free-solid";
import Header from "../../components/Auth/Header";
import Footer from "../../components/Auth/Footer";
import { ForgotPasswordEmailPhone } from "../../lib/axios";

function ForgotPassword() {
    const [spinner, setSpinner] = useState(false);
	const [email, setEmail] = useState('');
	const [selectRadio, setSelectRadio] = useState('email');
	// const [password, setPassword] = useState('');
	const [phone, setPhone] = useState('');
	const [isSaving, setIsSaving] = useState(false);

	const handleForgotPassword = (e) => {
		
		if(selectRadio === ''){
			Swal.fire({
				icon: 'error',
				title: "Please select Email Or Phone",
				showConfirmButton: false,
				timer: 1500
			})					
		}else{
			setIsSaving(true);
		
			let data = "";
	
			if(selectRadio == "email"){
				data = {
					'emailAddress' : email,
					'isForgetPassword' : true,
				}
			}else{
				data = {
					'userContactNumber' : phone,
					'isForgetPassword' : true,
				}
			}
	
			ForgotPasswordEmailPhone(data)
			.then(function (response) {
					
				Swal.fire({
					icon: 'success',
					title: response.responseMessage,
					showConfirmButton: false,
					timer: 1500
				})
				
					setIsSaving(false);
					setPhone('');
					setEmail('');
				})
				.catch(function (error) {
					Swal.fire({
						icon: 'error',
						title: error.response.data.responseMessage,
						showConfirmButton: false,
						timer: 1500
					})
					setIsSaving(false)
				});
		}
	}
  
	return (
		// <div classNameName="login">
		<>
		{/* <!-- header section strats --> */}
		{spinner ? (
			<h3>Loading...</h3>
		) : (
			<div className="hero_area">
			<Header />

			<section className="registration_page">
			<div className="login_wrapper">
				<div className="login_box">
					<div className="login_box_item">
						<div className="login_logo text-center">
							{/* <img src="./images/logo.png" alt="Logo" /> */}
							<img src={logo1} alt="Logo" />
						</div>
						<div className="login_form">
							<div className="h6 text-light pb5">Try again 
							{/* <a href="./login.html" className="font-weight-bold text-light h6">Login</a> */}
							<Link to="/login" className="font-weight-bold text-light h6"> Login</Link>
							</div>
							<form>
								<div className="forgot_pwd_box">
									<div className="forgot_pwd_itm">
										<div className="forgot_pwd_sub_itm has_radio">
											<input type="radio" name="verify_type" className="cPointer" id="ver_typ_email" value="email" checked={selectRadio==='email'?true:false} onChange={(e) => {setSelectRadio(e.target.value)}} />
											
											<label htmlFor="ver_typ_email" className="text-light cPointer">E-mail</label>
										</div>
										<div className="forgot_pwd_sub_itm has_radio_box">
											{/* <input type="email" name="email" className="form-control" placeholder="E-mail" /> */}
											<input type="email" name="email" value={email} onChange={(e)=>{setEmail(e.target.value)}} className="form-control" placeholder="E-mail" disabled={selectRadio==='phone'?true:false}/>
										</div>
									</div>
									<div className="forgot_pwd_itm">
										<div className="forgot_pwd_sub_itm has_radio">
											<input type="radio" name="verify_type" className="cPointer" id="ver_typ_phone" value="phone" checked={selectRadio==='phone'?true:false} onChange={(e) => {setSelectRadio(e.target.value)}} />
											<label htmlFor="ver_typ_phone" className="text-light cPointer">Phone</label>
										</div>
										<div className="forgot_pwd_sub_itm has_radio_box">
											<input type="text" name="phone" value={phone} onChange={(e)=>{setPhone(e.target.value)}} className="form-control" placeholder="Phone" disabled={selectRadio==='email'?true:false} />
											{/* <input type="text" name="phone" className="form-control" placeholder="Phone" /> */}
										</div>
									</div>
									<div className="forgot_pwd_itm" style={{width:'70%'}}>
										<div className="fgt_pwd_otp_btn">
											<input type="button" disabled={isSaving} className="btn btn-sm btn-warning" onClick={handleForgotPassword} value="Send OTP"/>
										</div>
										<div className="fgt_pwd_otp_box">
											<input type="text" name="otp" className="form-control" placeholder="OTP" />
										</div>
									</div>
								</div>
								<div className="form-group text-center">
									<input type="button" name="recovery" value="Verify OTP" className="recovery_submit_btn btn btn-sm btn-light" />
								</div>
							</form>
						</div>
					</div>
				</div>
			</div>
		</section>

			{/* <!-- info section --> */}
			<Footer />
			</div>
		)}
		</>
	);
}

export default ForgotPassword;
