import telephone from "../../images/telephone-symbol-button.png";
import logo from "../../images/logo.png";
import logo1 from "../../images/logo1.jpeg";
import { Link, useNavigate } from "react-router-dom";
import $ from "jquery";

function Header() {


  $('#topMenuBar').click(function(){
		$('.top_menu').toggleClass('top_menu_active');
		$(this).children('i').toggleClass('fa-bars');
		$(this).children('i').toggleClass('fa-times');
	});
  return (

    <header className="header_section">
    <div className="container">
      <div className="top_contact-container">
        <div className="tel_container">
          <a href="#">
            <img src={telephone} alt="" /> 6303476264
          </a>
        </div>
        <div className="social-container">
          <span className="ptnr_with_us p-1">
            <a
              href="#"
              className="text-light bg-dark p-1 rounded "
            >
              Partner with Us
            </a>
            <ul className="partner_item_box">
              <li className="partner_items">
                <a href="./amb-partner.html">I am Ambulance Owner</a>
              </li>
              <li className="partner_items">
                <a href="./dia-partner.html">I have Diagnostic Center</a>
              </li>
              <li className="partner_items">
                <a href="./pharma-partner.html">I have Pharmacy</a>
              </li>
              <li className="partner_items">
                <a href="./care-partner.html">Primary Care Specialties</a>
              </li>
            </ul>
          </span>
          <span className="p-1">

          <Link to="/user-login" className="text-light bg-dark p-1 rounded">Login/Register</Link>
          </span>
          <span className="p-1">

          <Link to="/send-otp" className="text-light bg-dark p-1 rounded">Send OTP Email/Mobile</Link>
          </span>
          {/* <a href="./login.html" className="text-light bg-dark p-1 rounded">
            {" "}
            Login/Register
          </a> */}
        </div>
      </div>
    </div>
    <div className="container-fluid">
      <nav className="navbar navbar-expand-lg custom_nav-container pt-3">
        <a className="head_logo mobBars" href="#">
          <img src={logo1} alt="" />
        </a>
        <span
          className="navbar-toggler m-0 p-0"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <i className="fas fa-bars text-light" id="mobMenuBars"></i>
        </span>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <div className="d-flex position-relative flex-column flex-lg-row align-items-center w-100 justify-content-between">
            <form
              className="form-inline"
              id="searchForm"
            >
              <select
                name="service"
                className="search_service form-control"
                id=""
                required
              >
                <option value="">Services...</option>
                <option value="1">Medicine</option>
                <option value="2">Diagnostic</option>
                <option value="3">Ambulance</option>
                <option value="4">Primary Care</option>
              </select>
              <input
                type="search"
                className="search_box form-control dNone"
                placeholder="Enter Medicine Name"
                
              />
              <input
                type="text"
                name="location"
                className="search_location form-control"
                placeholder="Location"
                required
              />
              <button type="submit" className="search_btn form-control">
                <i className="fas fa-search"></i>
              </button>
              <input
                type="submit"
                name="search"
                value="Search"
                className="mobSearch form-control"
              />
            </form>
            <div className="top_menu_btn ml-0 ml-lg-5">
              <span className="btn btn-light cPointer" id="topMenuBar">
                
                <i className="fas fa-bars"></i>
              </span>
            </div>
            <div className="top_menu position-absolute">
              <ul className="top_menu_box">
                <li className="top_menu_item" title="Services">
                  <a href="#">Services</a>
                </li>
                <li className="top_menu_item" title="About Us">
                  <a href="./about.html">About Us</a>
                </li>
                <li className="top_menu_item" title="Contact Us">
                  <a href="./contact.html">Contact Us</a>
                </li>

                <li className="top_menu_item width500 ptnr_mobile">
                  <span className="ptnr_mobile_btn">
                    Partner with Us <i className="fas fa-plus"></i>
                  </span>
                  <div className="ptnr_mobile_box dNone pl-2">
                    <div>
                      <a href="./amb-partner.html">I am Ambulance Owner</a>
                    </div>
                    <div>
                      <a href="./dia-partner.html">
                        I have Diagnostic Center
                      </a>
                    </div>
                    <div>
                      <a href="./pharma-partner.html">I have Pharmacy</a>
                    </div>
                    <div>
                      <a href="./care-partner.html">
                        Primary Care Specialties
                      </a>
                    </div>
                  </div>
                </li>
                <li className="top_menu_item" title="Logout">
                  {/* <!-- <a href="./login.html" className="width500">Login</a> --> */}
                  {/* <!-- when user login show logout or login --> */}
                  <a href="./login.html">Logout</a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </nav>
    </div>
  </header>   
    

  );
}

export default Header;
