import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom"
import telephone from "../../images/telephone-symbol-button.png";
import location from "../../images/location-white.png";
import logo from "../../images/logo.png";
import logo1 from "../../images/logo1.jpeg";
import Swal from 'sweetalert2'
// import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
// import { faCoffee } from "@fortawesome/fontawesome-free-solid";
import Header from "../../components/Auth/Header";
import Footer from "../../components/Auth/Footer";
import { Register, EmailVerify, PhoneVerify, SendEmailOTP, SendPhoneOTP } from "../../lib/axios";

function VerifyEmailMobile() {
  const navigate = useNavigate();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [phone, setPhone] = useState('');
  const [emailOTP, setEmailOTP] = useState('');
  const [phoneOTP, setPhoneOTP] = useState('');
  const [isSaving, setIsSaving] = useState(false);

	const setLocalEmailAndPhone = () => {
		setEmail(localStorage.getItem('email'));
		setPhone(localStorage.getItem('phone'));
	}

  	useEffect(() => {
		setLocalEmailAndPhone();
  	});

  const handleEmailVerify = () => {
	
	setIsSaving(true);
	let data = {
		'otp' : emailOTP,
		'email' : email,
	}
	
	EmailVerify(data)
	.then(function (response) {
		Swal.fire({
			icon: 'success',
			title: response.responseMessage,
			showConfirmButton: false,
			timer: 1500
		})
		setIsSaving(false);
		setEmail('');
		setEmailOTP('');
		navigate('/login');
	})
	.catch(function (error) {
		Swal.fire({
			icon: 'error',
			title: error.response.data.responseMessage,
			showConfirmButton: false,
			timer: 1500
		})
		setIsSaving(false)
	});        	
  }

  const handlePhoneVerify = () => {
	
	setIsSaving(true);
	let data = {
		'otp' : phoneOTP,
		'phoneNumber' : phone,
	}
	
	PhoneVerify(data)
	.then(function (response) {
		Swal.fire({
			icon: 'success',
			title: response.responseMessage,
			showConfirmButton: false,
			timer: 1500
		})
		setIsSaving(false);
		setPhone('');
		setPhoneOTP('');
		navigate('/login');
	})
	.catch(function (error) {
		Swal.fire({
			icon: 'error',
			title: error.response.data.responseMessage,
			showConfirmButton: false,
			timer: 1500
		})
		setIsSaving(false)
	});	
            
  }

  return (
    // <div classNameName="login">
    <div className="hero_area">
      {/* <!-- header section strats --> */}
      <Header />

      
      <section className="verify_email_mobile registration_page">
		<div className="login_wrapper">
			<div className="login_box">
				<div className="login_box_item">
					<div className="login_logo text-center">
						<img src={logo1} alt="Logo" />
					</div>
					<div className="login_form">
						<div className="h6 text-light font-weight-bold pb5">
							<span>Verification </span>
							<Link to="/login" className="float-right text-light h6">Login here</Link>
						</div>
						<form>
							
							<div className="form-group hasOtp">
								
								<input type="text" className="form-control otpBox" value={emailOTP} name="emailOTP" onChange={(e)=>{setEmailOTP(e.target.value)}} placeholder="Email OTP" required />								
								<input type="button" disabled={isSaving} className="btn btn-sm btn-primary verifyBtn" value="Verify" onClick={handleEmailVerify}/>								
							</div>
							<div className="form-group hasOtp">
								
								<input type="text" className="form-control otpBox" value={phoneOTP} name="phoneOTP" onChange={(e)=>{setPhoneOTP(e.target.value)}} placeholder="Phone OTP" required />
								<input type="button" disabled={isSaving} className="btn btn-sm btn-primary verifyBtn" value="Verify" onClick={handlePhoneVerify} />
							</div>
						</form>
					</div>
				</div>
			</div>
		</div>
	</section>

      {/* <!-- info section --> */}
	  <Footer/>
    </div>
  );
}

export default VerifyEmailMobile;
