import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom"
import telephone from "../../images/telephone-symbol-button.png";
import location from "../../images/location-white.png";
import logo from "../../images/logo.png";
import logo1 from "../../images/logo1.jpeg";
import Swal from 'sweetalert2'
// import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
// import { faCoffee } from "@fortawesome/fontawesome-free-solid";
import Header from "../../components/Auth/Header";
import Footer from "../../components/Auth/Footer";
import { Register, EmailVerify, PhoneVerify, SendEmailOTP, SendPhoneOTP } from "../../lib/axios";

function Login() {
  const navigate = useNavigate();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [phone, setPhone] = useState('');
  const [emailOTP, setEmailOTP] = useState('');
  const [phoneOTP, setPhoneOTP] = useState('');
  const [isSaving, setIsSaving] = useState(false);



  const handleRegister = (e) => {

	setIsSaving(true);
	let data = {
		'userEmail' : email,
		'password' : password,
		'userPhoneNumber' : phone,
	}
	
	Register(data)
	.then(function (response) {
            
            Swal.fire({
                icon: 'success',
                // title: response.responseMessage,
				html: `
				We have sent OTP to your mail id,  mobile number. Please <a href="/verification	">Verify</a> to login to application
				`,
                showConfirmButton: true,
                // timer: 2500
            });
			let data = {
				email: email,
				phone: phone,
			};
			localStorage.setItem('email', email);
			localStorage.setItem('phone', phone);
            setIsSaving(false);
            setPhone('');
            setEmail('');
            setPassword('');
          })
          .catch(function (error) {
            Swal.fire({
                icon: 'error',
                title: error.response.data.responseMessage,
                showConfirmButton: false,
                timer: 1500
            })
            setIsSaving(false)
          });
  }

  const handleEmailVerify = () => {
	
	setIsSaving(true);
	let data = {
		'otp' : emailOTP,
		'email' : email,
	}
	
	EmailVerify(data)
	.then(function (response) {
		Swal.fire({
			icon: 'success',
			title: response.responseMessage,
			showConfirmButton: false,
			timer: 1500
		})
		setIsSaving(false);
		setEmail('');
		setEmailOTP('');
	})
	.catch(function (error) {
		Swal.fire({
			icon: 'error',
			title: error.response.data.responseMessage,
			showConfirmButton: false,
			timer: 1500
		})
		setIsSaving(false)
	});        	
  }

  const handlePhoneVerify = () => {
	
	setIsSaving(true);
	let data = {
		'otp' : phoneOTP,
		'phoneNumber' : phone,
	}
	
	PhoneVerify(data)
	.then(function (response) {
		Swal.fire({
			icon: 'success',
			title: response.responseMessage,
			showConfirmButton: false,
			timer: 1500
		})
		setIsSaving(false);
		setPhone('');
		setPhoneOTP('');
	})
	.catch(function (error) {
		Swal.fire({
			icon: 'error',
			title: error.response.data.responseMessage,
			showConfirmButton: false,
			timer: 1500
		})
		setIsSaving(false)
	});	
            
  }

  const handleSendEmailOTP = () => {
	
	setIsSaving(true);
	let data = {
		'email' : email,
		'isForgetPassword' : false,
	}
	
	SendEmailOTP(data)
	.then(function (response) {
		Swal.fire({
			icon: 'success',
			title: response.responseMessage,
			showConfirmButton: false,
			timer: 1500
		})
		setIsSaving(false);
	})
	.catch(function (error) {
		Swal.fire({
			icon: 'error',
			title: error.response.data.responseMessage,
			showConfirmButton: false,
			timer: 1500
		})
		setIsSaving(false)
	});	
            
  }
    
  const handleSendPhoneOTP = () => {
	
	setIsSaving(true);
	let data = {
		'phoneNumber' : phone,
		'isForgetPassword' : false,
	}
	
	SendPhoneOTP(data)
	.then(function (response) {
		Swal.fire({
			icon: 'success',
			title: response.responseMessage,
			showConfirmButton: false,
			timer: 1500
		})
		setIsSaving(false);
	})
	.catch(function (error) {
		Swal.fire({
			icon: 'error',
			title: error.response.data.responseMessage,
			showConfirmButton: false,
			timer: 1500
		})
		setIsSaving(false)
	});	
            
  }

  return (
    // <div classNameName="login">
    <div className="hero_area">
      {/* <!-- header section strats --> */}
      <Header />

      
      <section className="registration_page">
		<div className="login_wrapper">
			<div className="login_box">
				<div className="login_box_item">
					<div className="login_logo text-center">
						<img src={logo1} alt="Logo" />
					</div>
					<div className="login_form">
						<div className="h6 text-light font-weight-bold pb5">
							<span>Already Registred </span>
							{/* <a href="javascript:void(0)" className="float-right text-light h6">Login here</a> */}
							<Link to="/login" className="float-right text-light h6">Login here</Link>
						</div>
						<form>
							
							<div className="form-group">
								<input type="email" name="email" value={email} onChange={(e)=>{setEmail(e.target.value)}} className="form-control intpBox" placeholder="E-mail Address" />
								{/* <input type="button" disabled={isSaving} className="btn btn-sm btn-warning sendOtpBtn" value="Send OTP" onClick={handleSendEmailOTP}/> */}
								{/* <input type="text" className="form-control otpBox" value={emailOTP} name="emailOTP" onChange={(e)=>{setEmailOTP(e.target.value)}} placeholder="OTP" required /> */}
								{/* <input type="button" disabled={isSaving} className="btn btn-sm btn-primary verifyBtn" value="Verify" onClick={handleEmailVerify}/> */}
							</div>
							<div className="form-group">
								<input type="text" name="phone" value={phone} onChange={(e)=>{setPhone(e.target.value)}} className="form-control intpBox" placeholder="Phone No." />
								{/* <input type="button" disabled={isSaving} className="btn btn-sm btn-warning sendOtpBtn" value="Send OTP" onClick={handleSendPhoneOTP}/> */}
								{/* <input type="text" className="form-control otpBox" value={phoneOTP} name="phoneOTP" onChange={(e)=>{setPhoneOTP(e.target.value)}} placeholder="OTP" required /> */}
								{/* <input type="button" disabled={isSaving} className="btn btn-sm btn-primary verifyBtn" value="Verify" onClick={handlePhoneVerify} /> */}
							</div>
							<div className="form-group">
								<input type="password" name="pwd" value={password} onChange={(e)=>{setPassword(e.target.value)}} className="form-control" autoComplete="off" placeholder="Password" required />
							</div>
							<div className="form-group text-center">
								<input type="submit" name="registrer" disabled={isSaving} value="Registrer" className="loginBtn btn btn-primary" onClick={handleRegister}/>
							</div>
						</form>
					</div>
				</div>
			</div>
		</div>
	</section>

      {/* <!-- info section --> */}
	  <Footer/>
    </div>
  );
}

export default Login;
