import React, {useLayoutEffect, useState} from 'react'
import { GetAllDiagnosticCenters, GetAllStores } from '../../lib/axios';

const DiagnosticServiceList = () => {

    const [serviceList, setServiceList] = useState(null);

    const handleGetAllDiagnosticCenters = () => {
        GetAllDiagnosticCenters()
            .then((response) => {
                setServiceList(response.diagnosticCenters);
            })
    }
    useLayoutEffect(() => {
        handleGetAllDiagnosticCenters();
    },[]);

    // console.log(serviceList);

    return (
        <div className='service-top'>
            <div class="row pt-4">
                
                    <div class="d_box table-responsive">
                        <form action="" method="POST" >
                            <div class="modal-body">
                                <table class="table table-striped">
                                    <thead class="thead-dark">
                                        <tr class="text-center">
                                            <th scope="col">serviceId</th>
                                            <th scope="col">serviceName</th>
                                            <th scope="col">userId</th>
                                            <th scope="col">storeId</th>
                                            
                                        </tr>
                                    </thead>

                                    <tbody>
                                                {                                                  
                                                    serviceList?.map((val, key) => (
                                                        <tr class="text-center">
                                                            <th scope="row">{val.serviceId}</th>
                                                            <td>{val.serviceName}</td>
                                                            <td>{val.userId}</td>
                                                            <td>{val.storeId}</td>
                                                        </tr>
                                                    ))                                                  
                                                }
                                    </tbody>
                                </table>
                            </div>
                        </form>
                    </div>
                </div>
                <div class="col-md-3 col-sm-2 col-xs-12"></div>
            </div>
    )
}

export default DiagnosticServiceList
