import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import logo1 from "../../images/logo1.jpeg";
import Swal from "sweetalert2";
// import InputLogin from "../../components/login/InputLogin";

import { loginUser } from "../../lib/axios";

function LoginInput() {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [isSaving, setIsSaving] = useState(false);
  const [spinner, setSpinner] = useState(false);

  const handleLogin = (e) => {
    setIsSaving(true);
    let data = {
      email: email,
      password: password,
    };

    loginUser(data)
      .then(function (response) {
        localStorage.setItem("user", JSON.stringify(response));
        localStorage.setItem("email", response.userEmailAddress);
        // localStorage.setItem("token", response.token);

        Swal.fire({
          icon: "success",
          title: response.responseMessage,
          showConfirmButton: false,
          timer: 2500,
        });
        setIsSaving(false);
        setEmail("");
        setPassword("");
        navigate("/storemanager");
      })
      .catch(function (error) {
        Swal.fire({
          icon: "error",
          title: error.response.data.responseMessage,
          showConfirmButton: false,
          timer: 1500,
        });
        setIsSaving(false);
      });
  };

  return (
    <>
      <div className="login_wrapper">
        <div className="login_box">
          <div className="login_box_item">
            <div className="login_logo text-center">
              <img src={logo1} alt="Logo" />
            </div>
            <div className="login_form">
              <div className="h4 text-light font-weight-bold pb5">
                Login
                <Link to="/register" className="float-right text-light h6">
                  Register Now
                </Link>
              </div>
              <form>
                <div className="form-group">
                  <input
                    type="email"
                    value={email}
                    name="username"
                    className="form-control"
                    placeholder="Username"
                    onChange={(event) => {
                      setEmail(event.target.value);
                    }}
                    required
                  />
                </div>
                {/* <InputField
                    type={"email"}
                    name={"Username"}
                    value={email}
                    handleChange={(e) => setEmail(e.target.value)}
                /> */}
                <div className="form-group">
                  <input
                    type="password"
                    value={password}
                    name="password"
                    className="form-control"
                    placeholder="Password"
                    autoComplete="off"
                    onChange={(event) => {
                      setPassword(event.target.value);
                    }}
                    required
                  />
                </div>
                <div className="form-group text-right">
                  <Link to="/forgot-password" className="text-light h6">
                    Forgot Password?
                  </Link>
                </div>
                <div className="form-group text-center">
                  <input
                    disabled={isSaving}
                    type="submit"
                    name="login"
                    value="Login"
                    onClick={handleLogin}
                    className="loginBtn btn btn-primary"
                  />
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default LoginInput;
