import location from "../../images/location-white.png";

function Footer() {
  return (
    <section className="info_section layout_padding2">
      <div className="container">
        <div className="row">
          <div className="col-md-3">
            <div className="info_contact">
              <h4>Contact</h4>
              <div className="box">
                <div className="img-box">
                  <img src={location} alt="" />
                </div>
                <div className="detail-box">
                  <h6>
                    Kosuri health care pvt ltd, Kandukuru, Nellore Dist , AP PIN
                    – 523105
                  </h6>
                </div>
              </div>
            </div>
          </div>
          <div className="col-md-3">
            <div className="info_menu">
              <h4>Menu</h4>
              <ul className="navbar-nav">
                <li className="nav-item active">
                  <a className="nav-link" href="./index.html">
                    Home <span className="sr-only">(current)</span>
                  </a>
                </li>
                <li className="nav-item">
                  <a className="nav-link" href="./about.html">
                    {" "}
                    About{" "}
                  </a>
                </li>
                <li className="nav-item">
                  <a className="nav-link" href="./login.html">
                    {" "}
                    Login{" "}
                  </a>
                </li>
                <li className="nav-item">
                  <a className="nav-link" href="./registration.html">
                    {" "}
                    Register{" "}
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <div className="col-md-6">
            <div className="info_news">
              <h4>newsletter</h4>
              <form action="">
                <input type="text" placeholder="Enter Your email" />
                <div className="d-flex justify-content-center justify-content-end mt-3">
                  <button>Subscribe</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Footer;
